<template>
    <div>
        <div class="page-title py-5"></div>
        <common-table
            ref="custom_table"
          :headers="table_headers"
          :api="table_api"
          :filters="table_filters"
          :actions="table_actions"
            :json_columns="table_json_columns"
          @action="onClickAction"
        ></common-table>
    </div>
</template>

<script>
  import CommonTable from '../components/table/CommonTable'
  import { getMenus, deleteMenu } from '../api'
  export default {
    name: 'Menus',
    components: { CommonTable },
    data: () => ({
      current_menu_id: null,
      table_headers: [
        {
          text: 'id',
          align: 'left',
          sortable: false,
          value: 'id',
        },
        { text: '', value: 'content', sortable: false},
        { text: '', value: 'remark', sortable: true },
        { text: '', value: 'created_at', sortable: true },
        { text: '', value: 'updated_at', sortable: true },
        { text: '', value: 'action', sortable: false, align: 'center' },
      ],
        table_api: getMenus,
        table_filters: [
        {
          name: 'remark',
          title: '',
          value: '',
          type: 'text',
          options: [],
          compare: 'like',
          sign: 'where',
        },
        {
          name: 'updated_at',
          title: '',
          value: '',
          type: 'date',
          options: [],
          compare: '>=',
          sign: 'where',
        },
        {
          name: 'updated_at',
          title: '',
          value: '',
          type: 'date',
          options: [],
          compare: '<=',
          sign: 'where',
        },
      ],
      table_json_columns: ['item.content'],
      table_actions: [
        {
          sign: 'edit',
          text: '',
          tip: '',
          icon: 'pencil'
        },
        {
          sign: 'delete',
          text: '',
          tip: '',
          icon: 'delete'
        },
      ]
    }),
    mounted() {
      this.$VM.$on('onDialogConfirm', this.onDialogConfirm);
    },
    beforeDestroy() {
      this.$VM.$off('onDialogConfirm', this.onDialogConfirm);
    },
    methods: {
      onClickAction(params) {
        if (params.sign === 'edit') {
          this.$router.push({
            name: 'custom-menu',
            params: {
              menu_id: params.item.id
            }
          })
        } else if (params.sign === 'delete') {
          this.current_menu_id = params.item.id
          this.dialog = {
            show: true,
            title: '',
            text: '',
            sign: 'deleteMenu'
          }
          this.$store.commit('setDialog', this.dialog);
        }
      },
      onDialogConfirm(sign) {
        this.dialog.show = false
        this.$store.commit('setDialog', this.dialog);
        if (sign === 'deleteMenu') {
          deleteMenu(this.current_menu_id).then(() => {
            this.$store.commit('setSnackbar', {
              message: '',
              color: 'success',
              timeout: 1500,
              show: true
            })
            this.$refs.custom_table.init()
          })
        }
      }
    }
  }
</script>

